<template>
  <div id="app" :style="{ backgroundImage: 'url(' + backgroundUrl + ')' }">
    <div class="search-box">
      <input v-model="city" @keyup.enter="fetchWeather" placeholder="Enter city name" type="text" />
    </div>
    <div class="weather-info" v-if="weatherData">
      <h2>{{ weatherData.name }}</h2>
      <p>{{ weatherData.main.temp }}째C - {{ weatherData.weather[0].main }}</p>
      <p>Min Temp: {{ weatherData.main.temp_min }}째C</p>
      <p>Max Temp: {{ weatherData.main.temp_max }}째C</p>
    </div>
    <div class="recent-searches" v-if="recentSearches.length">
      <h3>Recent Searches:</h3>
      <ul>
        <li v-for="search in recentSearches" :key="search.city">
          {{ search.city }}: {{ search.temp }}째C
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      city: '',
      weatherData: null,
      backgroundUrl: require('@/assets/default.png'), // default background
      recentSearches: [] // Array to store recent searches
    };
  },
  methods: {
    async fetchWeather() {
      const apiKey = '02d42e0520ce9c2a8ddc9fe1cec80ce7';  // Replace YOUR_API_KEY with your actual OpenWeatherMap API key
      const url = `https://api.openweathermap.org/data/2.5/weather?q=${this.city}&units=metric&appid=${apiKey}`;
      try {
        const response = await axios.get(url);
        this.weatherData = response.data;
        this.updateBackground(this.weatherData.weather[0].main);
        this.addToRecentSearches(this.city, this.weatherData.main.temp);
      } catch (error) {
        console.error(error);
        this.weatherData = null;
      }
    },
    addToRecentSearches(city, temp) {
      const index = this.recentSearches.findIndex(item => item.city === city);
      if (index !== -1) {
        this.recentSearches.splice(index, 1);
      }
      this.recentSearches.unshift({ city, temp });
      if (this.recentSearches.length > 5) {
        this.recentSearches.pop();
      }
    },
    refreshRecentSearches() {
      this.recentSearches.forEach(async (search, index) => {
        const apiKey = 'YOUR_API_KEY';
        const url = `https://api.openweathermap.org/data/2.5/weather?q=${search.city}&units=metric&appid=${apiKey}`;
        try {
          const response = await axios.get(url);
          this.recentSearches[index].temp = response.data.main.temp;
        } catch (error) {
          console.error('Failed to refresh weather data for', search.city, error);
        }
      });
    },
    updateBackground(weatherCondition) {
      const condition = weatherCondition.toLowerCase();
      const mapping = {
        'clouds': 'cloudy',
        'clear': 'sunny',
        'rain': 'rainy',
        'snow': 'snowy',
        'wind': 'windy',
      };
      const imageFile = mapping[condition] ? mapping[condition] : 'default';
      this.backgroundUrl = require(`@/assets/${imageFile}.png`);
    }
  },
  mounted() {
    setInterval(this.refreshRecentSearches, 60000);
  }
}
</script>

